<template>
    <div class="not-found">
        <div class="commodity-detail-head">
            <div class="commodity-detail-head-wrap" @click="forword"><span class="iconfont head-icon">&#xe669;</span></div>
        </div>
        <div class="not-found-wrap">
            <router-link class="back-home" to="/"></router-link>
            <img class="not-found-pic" :src="notFoundPic" alt="404">
        </div>
        <div class="not-found-message">很抱歉，页面暂时未找到</div>
    </div>
</template>

<script>
export default {
    name: "NotFound",
    data() {
        return {
            notFoundPic: require('~static/images/404.png')
        }
    },
    methods: {
        forword() {
            this.$router.go(-1);
        }
    }
}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin'
    .not-found
        width 100%
        height 100% 
        position relative
        overflow hidden
        background-color $lightGray
        detail_top()
        &-wrap
            width 80%
            margin 1.5rem auto
            .back-home
                position absolute
                top 7rem
                left 0
                right 0
                width 1.8rem
                height .55rem
                margin 0 auto
            ^[-1]-pic
                width 100%
        &-message
            color #13222D
            text-align center
            font-weight bold
</style>



