<template>
    <div class="classify">
        <div class="classify-cont">
            <span>销量：</span>
            <van-switch v-model="checked_salesVolume" />
            <span class="classify-price">价格：</span>
            高<van-switch v-model="checked_price" />低
        </div>
        <ul class="classify-left">
            <li 
                v-for="(c,index) in classify" 
                :key="c.categoryId" 
                :class="['classify-link','border-bottom', active === index && 'active']" 
                @click="setIndex(index)"
                v-text="c.categoryName"
            ></li>
        </ul>
        <div class="classify-right" ref="classifyRight">
            <div class="classify-right-wrap border-left">
                <!-- 设置二级分类 -->
                <commodity
                    v-for="(item,index) in classify"
                    :key="item.categoryId"
                    v-show="active == index"
                    :smallClassify="item.categoryEnglishName"   
                    :commoditys="item.commoditys"
                    :checked_salesVolume="checked_salesVolume"
                    :checked_price="checked_price"
                ></commodity>
            </div>
        </div>
    </div>
</template>

<script>
import Commodity from './components/Commodity'
export default {
    name: 'CommodityCategory',
    props: {
        classify: {
            type: Array,
            required: true,
            default() {
                return []
            }
        }
    },
    data() {
        return {
            checked_salesVolume: false,
            checked_price: false,
            active: 0
        }
    },
    components: {
        Commodity
    },
    methods: {
        setIndex(index) {
            this.active = index;
            this.$refs.classifyRight.scrollTop = 0;
        }
    }
}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin'
    .classify
        &-cont
            align_vertical()
            color $darkGray
            width 100%
            height .8rem
            font-size .3rem
            background-color $white
            ^[0]-price
                margin-left .5rem
            & >>> .van-switch
                font-size .3rem!important
        & >>> .van-tab__pane
            overflow hidden
        &-left
                float left
                width 20%
                font-size .26rem
                border-top .08rem solid $lightGray
                ^[0]-link
                    display block
                    width 100%
                    vertical(.8rem)
                .active
                    color $white
                    background-color $violet
        &-right
                lateral_drag(80%,hidden,auto)
                float left
                height 10rem
                background-color #EFEFEF
                border-top .02rem solid #EFEFEF
                &-wrap
                    float left
</style>



