<template>
  <div id="app">
    <div class="app-wrap">
      <keep-alive :exclude="['Commodity','HomeCommodityDetail','ClassifyCommodityDetail']">
        <router-view/>
      </keep-alive>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<style lang="stylus">
  @import '~stylus/mixin'
  #app
    max_width()
    height 100%
    font-family 'Avenir', Helvetica, Arial, sans-serif
    -webkit-font-smoothing antialiased
    -moz-osx-font-smoothing grayscale
    text-align center
    color #2c3e50
    font-size 16px
    background-color #EFEFEF
    .app-wrap
      height 100%
      padding-bottom 1rem
      box-sizing border-box
</style>



