<template>
    <div class="order-nav">
        <div class="order-nav-tit border-bottom">
            <span class="my-order">我的订单</span>
            <router-link class="all-order" to="/member_center/all_order">查看全部订单</router-link>
        </div>
        <ul class="order-nav-list border-bottom">
            <li class="order-nav-item" v-for="item in orderList" :key="item.orderId">
                <div :class="['order-nav-badge', badgeFlag(item.orderBadge) && 'no-badge']" v-text="item.orderBadge"></div>
                <router-link class="order-nav-link" :to="item.orderLink">
                    <div class="iconfont order-nav-icon" v-html="item.orderIcon"></div>
                    <div v-text="item.orderTxt"></div>
                </router-link>
            </li>
        </ul>
    </div>    
</template>

<script>
export default {
    name: "MemberCenterNav",
    data() {
        return {
            orderList: [
                {
                    orderId: "o1",
                    orderIcon: "&#xe608;",
                    orderTxt: "待付款",
                    orderBadge: 0,
                    orderLink: "/member_center/pending_payment",
                },{
                    orderId: "o2",
                    orderIcon: "&#xe600;",
                    orderTxt: "待配送",
                    orderBadge: 23,
                    orderLink: "/member_center/no_delivery"
                },{
                    orderId: "o3",
                    orderIcon: "&#xe659;",
                    orderTxt: "配送中",
                    orderBadge: 11,
                    orderLink: "/member_center/distribution"
                },{
                    orderId: "o4",
                    orderIcon: "&#xe614;",
                    orderTxt: "待评价",
                    orderBadge: 13,
                    orderLink: "/member_center/no_evaluate"
                }
            ]
        }
    },
    methods: {
        badgeFlag(char) {
            return char <= 0;
        }
    }
}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin'
    .order-nav
        border_bold(before)
        background-color $white
        &-tit
            vertical(1rem)
            font-size .3rem
            padding 0 .3rem
            margin-top -.2rem
            .my-order
                color $darkGray
                float left
            .all-order
                color $gray
                float right
        &-list
            height 1.62rem
            ^[0]-item
                width 25%
                float left
                padding-top .3rem
                padding-bottom .2rem
                background-color $white
                position relative
                ^[0]-badge
                    vertical(.3rem)
                    color $white
                    position absolute
                    top 0.05rem
                    right 0.26rem
                    width .3rem
                    font-size .24rem
                    border .04rem solid $darkRed
                    border-radius 50%
                    background-color $darkRed
                    &.no-badge
                        display none
                ^[0]-link
                    color #555
                    display block
                    font-size .26rem
                    height 0
                    padding-bottom 58%
                    background-color $white
                    ^[0]-icon
                        color $darkGray
                        font-size .6rem
                        margin-bottom .2rem
</style>



