<template>
    <div class="category">
        <head-search></head-search>
        <van-tabs v-model="activeIndex" @click="setIndex">
            <van-tab v-for="item in categorys" :key="item.classifyId" :title="item.classifyName">
                <commodity-category :classify="classify"></commodity-category>
            </van-tab>
        </van-tabs>
        <footer-nav></footer-nav>
    </div>
</template>

<script>
import { request } from '@/assets/javascript/request.js'
import { mapMutations } from 'vuex'
import HeadSearch from '@/common/HeadSearch'
import CommodityCategory from './components/CommodityCategory'
import FooterNav from '@/common/FooterNav'
export default {
    name: 'Classify',
    data() {
        return {
            categorys: []
        }
    },
    components: {
        HeadSearch,
        CommodityCategory,
        FooterNav
    },
    computed: {
        activeIndex: {
            get() {
                return this.$store.state.activeIndex;
            },
            set(newValue) {
                //此处留空，为了解决计算属性已分配，但提示没有setter的问题
            }
        },
        classify() {
            switch(this.activeIndex) {
                case 0:
                    this.setBigClassify(this.categorys[0].classifyEnglishName);     //设置一级分类
                    return this.categorys[0].fruitCategory;
                    break;
                case 1:
                    this.setBigClassify(this.categorys[1].classifyEnglishName);     //设置一级分类
                    return this.categorys[1].alcoholCategory;
                    break;
            }
        }
    },
    methods: {
        ...mapMutations(['setActiveIndex','setBigClassify']),
        init() {
            this.$axios.get(request.url.classify).then(this.getClassifyData);
        },
        getClassifyData(res) {
            if(res.status == 200 && res.data.data){
                this.categorys = res.data.data.classify;;
            }
        },
        setIndex(index) {
            this.setActiveIndex(index);
        }
    },
    created() {
        this.init();
    }
}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin'
    .category
        max_width()
        height 100%
        padding-top 1rem
        overflow hidden
        background-color $white
        & >>> .van-tab--active
            color $violet
        & >>> .van-tabs__line
            background-color $violet
        & >>> .van-ellipsis
            overflow visible
            font-size .3rem
</style>




