<template>
    <div class="recommend">
        <h2 class="recommend-tit">
            <span class="iconfont icon-love">&#xe652;</span>猜您喜欢
        </h2>
        <ul class="recommend-list" :style="{ height: setHeight }">
            <li class="recommend-item" v-for="item in hotGoods" :key="item.goodsId">
                <router-link :to="`/home/hotGoods/${item.goodsId}`">
                    <div class="recommend-pic-wrap"><img :src="item.image" :alt="item.goodsName"></div>
                    <div class="recommend-wrap">
                        <h3 class="tit" v-text="item.goodsName"></h3>
                        <span class="price">&#165;<span v-text="item.mallPrice"></span></span>
                        <span class="origin-price">&#165;<span v-text="item.price"></span></span>
                    </div>
                </router-link>
            </li>
        </ul>
    </div>    
</template>

<script>
export default {
    name: "Recommend",
    props: {
        hotGoods: {
            type: Array,
            required: true
        }
    },
    computed: {
        setHeight() {
            let height = Math.ceil(this.hotGoods.length / 3) * 3.43;
            return height+'rem';
        }
    }
}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin'
    .recommend
        &-tit
            vertical(.8rem)
            color $darkRed
            font-size .3rem
            font-weight bold
            margin-top .4rem
            .icon-love
                font-size .46rem
                position relative
                top .05rem
                margin-right .06rem
        &-list
            padding-left 1%
            background-color $lightGray
            padding-bottom 13%
            ^[0]-item
                float left
                width 32%
                padding .2rem
                box-sizing border-box
                background-color $white
                margin-bottom 1%
                margin-right 1.3%
                ^[0]-pic-wrap
                    height 0
                    padding-bottom 100%
                    >img
                        display block
                        width 100%
                ^[0]-wrap
                    margin-top .3rem
                    .tit
                        ellipsis(2rem)
                        color $violet
                        font-size .28rem
                        line-height .34rem
                    .price
                        color $darkRed
                        font-size .3rem
                    .origin-price
                        color $dary
                        font-size .24rem
                        text-decoration line-through
</style>



