<template>
    <ul class="classify-fruit">
        <li class="classify-fruit-item" v-for="c in categorys" :key="c.goodsId">
            <router-link :to="`/classify/${bigClassify}/${smallClassify}/${c.goodsId}`" tag="div">
                <div class="classify-fruit-wrap">
                    <img class="classify-fruit-pic" :src="c.image" :alt="c.goodsName">
                </div>
                <p class="fruit-desc" v-text="c.goodsName"></p>
                <div class="price">
                    <span class="shopping-price">&#165;<span v-text="c.mallPrice"></span></span>
                    <span class="price-wrap">&#165;<span v-text="c.price"></span></span>
                    <div class="price-wrap sales-volume">销量：<span v-text="c.salesVolume"></span></div>
                </div>
            </router-link>
        </li>
    </ul>
</template>

<script>
import { mapState } from 'vuex'
import { obj } from '@/assets/javascript/common.js'
export default {
    name: 'Commodity',
    props: {
        commoditys: {
            type: Array,
            required: true,
            default() {
                return []
            }
        },
        checked_price: {
            type: Boolean,
            required: true
        },
        checked_salesVolume: {
            type: Boolean,
            required: true
        },
        smallClassify: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            categorys: obj.priceSort(this.commoditys,'salesVolume')
        }
    },
    computed: {
        ...mapState(['activeIndex','bigClassify']),
    },
    watch: {
        checked_price() {
            if(this.checked_price) {
                this.categorys = obj.priceSort(this.commoditys,'mallPrice');
            }else {
                this.categorys = obj.priceReverse(this.commoditys,'mallPrice')
            }
        },
        checked_salesVolume() {
            if(this.checked_salesVolume) {
                this.categorys = obj.priceReverse(this.commoditys,'salesVolume')
            }else {
                this.categorys = obj.priceSort(this.commoditys,'salesVolume');
            }
        }
    }
}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin'
    .classify-fruit
        float left
        margin-bottom 1.07rem
        padding .06rem .1rem
        box-sizing border-box
        &-item
            float left
            width 49%
            height 4rem
            font-size .28rem
            background-color $white
            margin-bottom .1rem
            &:nth-child(2n)
                margin-left .1rem
            ^[0]-wrap
                width 100%
                height 0
                padding-bottom 90%
                border 0.1rem solid $white
                box-sizing border-box
                overflow hidden
                ^[0]-pic
                    max-width 100%
                    height 2.5rem
            .fruit-desc
                color $violet
                margin-top .12rem
                margin-bottom .1rem
            .price
                .shopping-price
                    color $darkRed
                    font-size .3rem
                &-wrap
                    color $gray
                    font-size .24rem
                    text-decoration line-through
                .sales-volume
                    color #555
                    margin-top .08rem
                    margin-bottom .09rem
                    text-decoration none

</style>



