<template>
    <div class="nav-wrap border-top">
        <router-link v-for="nav in navData" :to="nav.route" class="nav-link" :key="nav.navId" tag="div">
            <div>
                <span class="iconfont icon-nav" v-html="nav.icon"></span>
                <div class="nav-txt" v-text="nav.name"></div>
            </div>
        </router-link>
    </div>    
</template>

<script>
export default {
    name: 'FooterNav',
    data() {
        return {
            navData: [
                {
                    navId: 'n1',
                    route: '/home',
                    icon: '&#xe602;',
                    name: '商城'
                },{
                    navId: 'n2',
                    route: '/classify',
                    icon: '&#xe62f;',
                    name: '分类'
                },{
                    navId: 'n3',
                    route: '/shopping_cart',
                    icon: '&#xe600;',
                    name: '购物车'
                },{
                    navId: 'n4',
                    route: '/member_center',
                    icon: '&#xe660;',
                    name: '会员中心'
                }
            ]
        }
    }
}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin'
    .nav-wrap
        max_width() 
        position fixed
        bottom 0
        height 1rem
        background-color $white 
        .nav-link
            float left
            width 25%
            height 100%
            align_vertical()
            .icon-nav
                color $darkGray
                font-size .44rem
            .nav-txt
                font-size .26rem
                margin-top .1rem
        .router-link-active
            .icon-nav
            .nav-txt
                color $violet
</style>




