
import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        activeIndex: 0,
        bigClassify: '',
        goodsArr: [],
        addressArr: [],
        addrIndex: 1,
        backRoute: '/member_center'
    },
    mutations: {
        setActiveIndex(state, index) {  //设置一级分类活动索引
            state.activeIndex = index;
        },
        setBigClassify(state,type) {    //设置一级分类
            state.bigClassify = type;
        },
        addGoods(state,goods) {     //添加商品
            let length = state.goodsArr.length;
            if(!length) {
                state.goodsArr.push(goods);
            }else {
                for(let i = 0; i < length; i++){
                    if(state.goodsArr[i].goodsId == goods.goodsId){
                        state.goodsArr[i].number+=goods.number;
                        return;
                    }
                }
                state.goodsArr.push(goods);
            }
        },
        delGoods(state,goods) {  //删除商品
            state.goodsArr = goods;
        },
        addAddr(state,addrObj) {     //添加收货地址
            if(addrObj.checked) {
                state.addressArr.forEach((item) => {
                    item.checked = false;
                })
                state.addressArr.unshift(addrObj);
            }else {
                state.addressArr.push(addrObj);
            }
        },
        setAddrIndex(state,index) {     //设置收货地址活动项索引
            state.addrIndex = index;
        },
        editAddr(state,addrObj) {    //编辑收货地址
            state.addressArr.forEach((item,index,arr) => {
                item.checked = false;
                item.id == addrObj.id && arr.splice(index,1);
            });
            state.addressArr.unshift(addrObj);
        },
        delAddr(state,idx) {    //删除收货地址
            state.addressArr.forEach((item,index,arr) => {
                item.id == idx && arr.splice(index,1);
            });
        },
        setBackRoute(state,route) {    //设置收货地址返回路由
            state.backRoute = route;
        }
    }
})


