let obj =  {
    priceSort(array,key) {
        return array.sort((a,b) => {
            return a[key] - b[key];
        })
    },
    priceReverse(array,key) {
        return array.sort((a,b) => {
            return b[key] - a[key];
        })
    }
}

export {obj}


