<template>
    <div class="search">
        <van-row class="search-wrap" type="flex" align="center">
            <van-col :span="3">
                <span class="iconfont location-icon">&#xe619;</span>
            </van-col>
            <van-col :span="15">
                <van-field v-model="value" placeholder="查找" class="border-bottom"></van-field>
            </van-col>
            <van-col :span="5">
                <van-button>搜索</van-button>
            </van-col>
        </van-row>
    </div>
</template>

<script>
export default {
  name: 'HeadSearch',
  data () {
    return {
      value: ''
    }
  }
}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin' 
    .search
        max_width()
        z-index 100
        position fixed
        top 0
        &-wrap
            height 1rem
            color $white
            background-color $violet
            position relative
            .location-icon
                display inline-block
                font-size .46rem 
                margin-top .06rem
            & >>> .van-cell
                background-color $violet
                padding 0 .15rem
                .van-field__control
                    height .6rem
            & >>> .van-field__control
                color $white
                placeholder()
            & >>> .van-button
                border_none()
                color #000
                width 1.4rem
                height .54rem
                line-height .6rem
                font-size 0.28rem
                background-color $white
                border-radius .25rem 
                margin-left .2rem        
</style>



