<template>
    <div class="shopping-cart-manage">
        <div class="no-cart" v-if="!length">
            <img class="no-cart-pic" :src="cart" alt="购物车竟然是空的" />
            <div class="no-cart-wrap">
                <p class="cart-tips">购物车竟然是空的</p>
                <p>再忙，也要记得买点什么犒赏自己~</p>
            </div>
        </div>
        <van-popup v-model="show" position="top" :overlay="false">尚未选中任何商品</van-popup>
        <van-cell class="border-top" v-if="length">
            <template slot="title">
                <van-checkbox v-model="allFlag">全选</van-checkbox>
            </template>
            <div slot="right-icon" v-if="result.manageFlag">
                <span>合计:
                    <span class="total-price">&#165; <span v-text="result.total"></span></span>
                </span>
                <van-button round @click="popupShow">结算(<span v-text="result.length"></span>)</van-button>
            </div>
            <div slot="right-icon" v-else>
                <van-button round @click="delchecked"><span class="iconfont icon-del">&#xe645;</span>删除</van-button>
            </div>
        </van-cell>
    </div>    
</template>

<script>
import { mapState,mapMutations } from 'vuex'
export default {
    name: 'ShoppingCartManage',
    data() {
        return {
            cart: require('~static/images/shopping_cart.png'),
            show: false
        }
    },
    props: {
        goodsArr: {
            type: Array,
            required: true,
            default() {
                return []
            }
        },
        result: {
            type: Object,
            required: true,
            default() {
                return {}
            }
        }
    },
    computed: {
        length() {
            return this.goodsArr.length;
        },
        allFlag: {
            get() {
                return this.result.flag;
            },
            set(newVal) {
                this.allChecked(newVal);
            }
        }
    },
    methods: {
        ...mapMutations(['delGoods']),
        allChecked(flag) {
            this.$emit('all-checked',flag);
        },
        popup() {   //提示信息弹层
            this.show = true;
            setTimeout(() => {
                this.show = false;
            },2000);
        },
        delchecked() {  //删除商品
            let checkArr = this.goodsArr.filter(item => item.goodsFlag == true);
            if(checkArr.length) {
                let noCheckArr = this.goodsArr.filter(item => item.goodsFlag == false);
                this.delGoods(noCheckArr);
                this.result.flag = false;
                this.$emit('refresh-data');
            }else {
                this.popup();
            }
        },
        popupShow() {
            let checkArr = this.goodsArr.filter(item => item.goodsFlag == true);
            checkArr.length > 0 ? this.$router.push('/settlement') : this.popup();
        }
    }
}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin'
    .shopping-cart-manage
        van_popup()
        .no-cart
            width 100%
            margin 0 auto
            height 0
            padding-bottom 95%
            position relative
            &-pic
                width 100%
            &-wrap
                color $gray
                position absolute
                right 0
                bottom .3rem   
                left 0
                font-size .28rem
                letter-spacing .01rem
                .cart-tips
                    color #999
                    font-size .32rem
                    margin-bottom .2rem
        & >>> .van-cell
            max_width()
            vertical(1.1rem)
            van_checkbox()
            position fixed
            right 0
            bottom 1rem
            left 0
            padding 0 .2rem
            text-align left
            font-size .32rem
            .van-checkbox__label
                color $darkGray 
                font-size .3rem
            .total-price
                color $violet
                margin-right .1rem
            .van-button
                vertical(.65rem)
                color $white
                background-color $violet
                border-color $violet
                font-size .28rem
                .icon-del
                    font-size .34rem
                    position relative
                    top .02rem
                    left -.1rem
</style>




