<template>
    <div class="mask-layer" v-if="layerFlag">
        <van-popup v-model="show" position="top" :overlay="false" v-text="address"></van-popup>
        <div class="layer">
            <div class="layer-head border-bottom">
                <div class="layer-head-wrap border">
                    <img class="layer-head-pic" :src="commodityItem.image" :alt="commodityItem.goodsName">
                </div>
                <div class="layer-head-cont">
                    <div class="layer-name" v-text="commodityItem.goodsName"></div>
                    <div class="unit-price">&#165;<span class="unit-price-wrap" v-text="commodityItem.mallPrice"></span></div>
                </div>
                <span class="iconfont layer-close" @click="layerClose">&#xe623;</span>
            </div>
            <div class="layer-region border-bottom">
                <h2>配送区域<span>(配送地可能会影响库存)</span></h2>
                <van-cell to="/address_manage" @click="setAddress">
                    <template slot="title">
                        <span class="iconfont region-icon">&#xe619;</span>
                        <span class="region-txt" v-text="address"></span>
                    </template>
                    <span slot="right-icon" class="iconfont region-r-icon">&#xe667;</span>
                </van-cell>
            </div>
            <div class="layer-region border-bottom">
                <h2>购买数量</h2>
                <van-cell>
                    <template slot="title">
                        <span class="remaining-number">剩余<span>123</span>件</span>
                    </template>
                    <van-stepper slot="right-icon" v-model="number" integer class="commodity-step"></van-stepper>
                </van-cell>
            </div>
            <div class="layer-confirm">
                <a class="confirm-btn" @click="confirmHandle">确定</a>
            </div>
        </div>    
    </div>    
</template>

<script>
import { mapState,mapMutations } from 'vuex'
export default {
    name: "MaskLayer",
    data() {
        return {
            number: 1,
            show: false
        }
    },
    props: {
        commodityItem: {
            type: Object,
            required: true,
            default() {
                return {}
            }
        },
        layerFlag: {
            type: Boolean,
            required: true,
            default: false
        }
    },
    computed: {
        ...mapState(['addressArr']),
        address() {
            return this.addressArr.length <= 0 ? '请选择收货地址' : (this.addressArr[0].checked ? this.addressArr[0].region : '请设置默认收货地址');
        }
    },
    methods: {
        ...mapMutations(['addGoods','setBackRoute']),
        layerClose() {
            this.$emit('close',false);
        },
        confirmHandle() {
            // 判断是否设置默认地址
            if(this.addressArr.length > 0 && this.addressArr[0].checked) {
                let result = {
                    ...this.commodityItem,
                    number: this.number,
                    goodsFlag: false
                }
                this.addGoods(result);
                this.$router.push('/shopping_cart');
            }else{      //提示信息弹层
                this.show = true;
                setTimeout(() => {
                    this.show = false;
                },2000);    
            }
        },
        setAddress() {
            this.setBackRoute(this.$route.path);
        }
    }
}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin'
    .mask-layer
        max_width() 
        full_screen(5,fixed,0,0,0,0,rgba(0,0,0,0.3))
        van_popup()
        .layer
            full_screen(6,absolute,20%,0,0,0,$white)
            padding 0 .3rem
            &-head
                height 1.7rem
                position relative
                &-wrap
                    position absolute
                    top -.4rem
                    width 33%
                    height 0
                    padding-bottom 25%
                    overflow hidden
                    background-color $lightGray
                    ^[-1]-pic
                        width 100%
                        height 1.8rem
                &-cont
                    margin-left 37%
                    text-align left
                    font-size .28rem
                    padding-top .15rem
                    .layer-name
                        ellipsis(85%)
                        font-size .32rem
                        line-height .34rem
                    .unit-price
                        color $violet
                        font-size .36rem
                        margin-top .15rem
                        &-wrap
                            font-size .4rem
                ^[-1]-close
                    color #969799
                    font-size .4rem
                    position absolute
                    top .2rem
                    right -.1rem
            &-region
                color #333
                padding-top .3rem
                padding-bottom .2rem
                font-size .3rem
                text-align left
                & >>> .van-cell
                    padding .2rem 0 0 
                    line-height .32rem
                    overflow visible
                    .region-icon
                    .region-txt
                    .region-r-icon
                        color $darkGray
                        font-size .3rem
                    .region-r-icon
                        font-weight bold
                    .remaining-number
                        color $gray
                        font-size .28rem
                & >>> .commodity-step
                    steper(.7rem,.7rem)
                    margin-top -.5rem
            &-confirm
                position absolute
                bottom 0
                left 0
                right 0
                .confirm-btn
                    vertical(1rem)
                    display block
                    color $white
                    font-size .32rem
                    background-color $red
</style>



