<template>
    <div class="hot">
        <h3 class="hot-tit border-bottom">商品推荐</h3>
        <div class="hot-box">
            <div class="hot-commodity" :style="{ width: maxWidth }">
                <router-link class="hot-commodity-link border-right" v-for="hot in hotGoods" :key="hot.goodsId" :to="`/home/hotGoods/${hot.goodsId}`">
                    <div class="hot-wrap"><img class="hot-pic" :src="hot.image" :alt="hot.goodsName"></div>
                    <div class="hot-name"><span v-text="hot.goodsName"></span></div>
                    <div class="hot-money">&#165;<span v-text="hot.mallPrice"></span></div>
                    <div class="del-money">&#165;<span v-text="hot.price"></span></div>
                    <div class="hot-sign">推荐</div>
                </router-link>
            </div> 
        </div>
    </div>    
</template>

<script>
export default {
    name: 'HotGoods',
    props: {
        hotGoods: {
            type: Array,
            required: true,
            default() {
                return []
            }
        }
    },
    computed: {
        maxWidth() {
            var _product = this.hotGoods.length * 2.5;
            return _product + 'rem';
        }
    }
}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin'
    .hot
        height 4.3rem
        padding-top .1rem
        background-color $white
        overflow hidden
        border_bold(before)
        &-tit
            color $darkRed
            font-size .3rem
            font-weight bold
            text-align left
            padding-left .2rem
            padding-bottom .2rem
        &-box
            lateral_drag(7.5rem,scroll,hidden)
            ^[-1]-commodity
                &-link
                    display inline-block
                    width 2.5rem
                    padding-top .05rem
                    padding-bottom .05rem
                    position relative
                    ^[0]-wrap
                        overflow hidden
                        width 100%
                        height 0
                        padding-bottom 100%
                        .hot-pic
                            width 100%
                    ^[0]-name
                        color $violet
                        text-align center
                        line-height .3rem
                        >span
                            ellipsis(2.2rem)
                            font-size .26rem
                            display inline-block
                    ^[0]-money
                        font-size .3rem
                    .del-money
                        color $gray 
                        font-size .26rem
                        text-decoration line-through
                    ^[0]-sign
                        align_vertical()
                        color $white 
                        font-size .2rem
                        position absolute
                        top 50%
                        right 0
                        width .7rem
                        height .28rem
                        margin-top -.15rem
                        border-radius .2rem 0 0 .2rem
                        background-color $darkRed
</style>




