<template>
    <div class="member-center">
        <div class="func-nav">
            <member-center-head></member-center-head>
            <member-center-nav></member-center-nav>
        </div>
        <div class="func-box">
            <van-cell-group class="func-wrap">
                <van-cell 
                    class="func-item" 
                    is-link 
                    v-for="(item,index) in funcList" 
                    :key="item.funcId"
                    :to="item.funcLink"
                    @click="setAddress(index)"
                >
                    <template slot="title">
                        <span :class="['func-icon', bgFlag(index) && 'func-icon-bg']">
                            <span class="iconfont func-icon-wrap" v-html="item.funcIcon"></span>
                        </span>
                        <span :class="['func-tit', funcFlag(index) && 'border-bottom']" v-text="item.funcTit"></span>
                    </template>
                    <span slot="right-icon" class="iconfont right-icon">&#xe667;</span>
                </van-cell>
            </van-cell-group>
        </div>
        <footer-nav></footer-nav>
    </div>
</template>

<script>
import { mapMutations } from 'vuex';
import MemberCenterHead from './components/MemberCenterHead'
import MemberCenterNav from './components/MemberCenterNav'
import FooterNav from '@/common/FooterNav'
export default {
    name: "MemberCenter",
    data() {
        return {
            funcList: [
                {
                    funcId: "func0",
                    funcIcon: "&#xe626;",
                    funcTit: "优惠券",
                    funcLink: "/coupon"
                },{
                    funcId: "func4",
                    funcIcon: "&#xe619;",
                    funcTit: "地址管理",
                    funcLink: "/address_manage"
                },{
                    funcId: "func2",
                    funcIcon: "&#xe7b6;",
                    funcTit: "福利包包",
                    funcLink: "/member_center/welfare"
                },{
                    funcId: "func1",
                    funcIcon: "&#xe60b;",
                    funcTit: "客服",
                    funcLink: "/customer_service"
                },{
                    funcId: "func3",
                    funcIcon: "&#xe63a;",
                    funcTit: "关于我们",
                    funcLink: "/member_center/about_us"
                },{
                    funcId: "func5",
                    funcIcon: "&#xe622;",
                    funcTit: "修改密码",
                    funcLink: "/member_center/change_password"
                },{
                    funcId: "func6",
                    funcIcon: "&#xe64c;",
                    funcTit: "退出登录",
                    funcLink: "/member_center/log_out"
                }
            ]
        } 
    },
    components: {
        MemberCenterHead,
        MemberCenterNav,
        FooterNav
    },
    methods: {
        ...mapMutations(['setBackRoute']),
        bgFlag(idx) {
            return idx % 3 == 2;
        },
        funcFlag(idx) {
            return idx < this.funcList.length-1;
        },
        setAddress(idx) {
            idx == 1 &&  this.setBackRoute(this.$route.path);
        }
    }
}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin'
    .member-center
        overflow hidden
        padding-top 5rem
        .func-nav
            max_width()
            z-index 2
            position fixed
            top 0
        & >>> .func-box
            height 100%
            overflow hidden
            padding-bottom 1rem
            border-top .08rem solid $lightGray
            .func-wrap
                border-bottom .2rem solid $lightGray
                .func-item
                    text-align left
                    position relative
                    &:after
                        border-style none
                    .func-icon
                        align_vertical()
                        float left
                        width .6rem
                        height .6rem
                        border-radius 50%
                        background-color #4C91ED
                        &-wrap
                            color $white
                            font-size .4rem
                        &.func-icon-bg
                            background-color $violet
                    .func-tit
                        height 1rem
                        line-height 1.1rem
                        position absolute
                        top 0
                        right 0
                        width 84%
                        font-size .3rem
                    .right-icon
                        vertical(1rem)
                        position absolute
                        top 0
                        right 0
                        color $gray
                        text-align center
                        width .8rem
                        font-weight lighter
</style>



