<template>
  <div class="home-wrap">
    <head-search></head-search>
    <home-swiper :slides="slides"></home-swiper>
    <home-tab :category="category"></home-tab>
    <hot-goods :hotGoods="hotGoods"></hot-goods>
    <shop-floor :floors="floorObj.floor1" :floorName="floorName.floor1" floorFlag="floor1">
      <span class="iconfont floor-slot">&#xe604;</span>
    </shop-floor>
    <shop-floor :floors="floorObj.floor2" :floorName="floorName.floor2" floorFlag="floor2">
      <span class="iconfont floor-slot">&#xe603;</span>
    </shop-floor>
    <shop-floor :floors="floorObj.floor3" :floorName="floorName.floor3" floorFlag="floor3">
      <span class="iconfont floor-slot">&#xe605;</span>
    </shop-floor>
    <footer-nav></footer-nav>
  </div>
</template>

<script>
import { request } from '@/assets/javascript/request.js'
import HeadSearch from '@/common/HeadSearch'
import HomeSwiper from '~home/components/HomeSwiper'
import HomeTab from '~home/components/HomeTab'
import HotGoods from '~home/components/HotGoods'
import ShopFloor from '~home/components/ShopFloor'
import FooterNav from '@/common/FooterNav'
export default {
  name: 'Home',
  data() {
    return {
      slides: [],
      category: [],
      hotGoods: [],
      floorName: {},
      floorObj: {}
    }
  },
  components: {
    HeadSearch,
    HomeSwiper,
    HomeTab,
    HotGoods,
    ShopFloor,
    FooterNav
  },
  methods: {
    init() {
      this.$axios.get(request.url.home).then(this.getHomeData);
    },
    getHomeData(res) {
      if(res.status == 200 && res.data.data){
        res = res.data.data;
        this.slides = res.slides;
        this.category = res.category;
        this.hotGoods = res.hotGoods;
        this.floorName = res.floorName;
        this.$set(this.floorObj,'floor1',res.floor1);
        this.$set(this.floorObj,'floor2',res.floor2);
        this.$set(this.floorObj,'floor3',res.floor3);
      }
    }
  },
  mounted() {
    this.init();
  }
}
</script>

<style lang="stylus" scoped>
  .home-wrap
    padding-top 1rem
    padding-bottom 1.4rem
    background-color #EFEFEF
</style>



