<template>
    <div class="shopping-cart-nav border-topbottom">
        <div class="small-func">
            <router-link to="/shop">
                <span class="iconfont small-func-icon">&#xe650;</span>
                <div>店铺</div>
            </router-link>
        </div>
        <div class="small-func">
            <router-link to="/customer_service">
                <span class="iconfont small-func-icon customer-service">&#xe6ed;</span>
                <div>客服</div>
            </router-link>
        </div>
        <div class="small-func">
            <div>
                <span class="iconfont small-func-icon collection">&#xe63b;</span>
                <div>收藏</div>
            </div>
        </div>
        <div class="shopping-cart-func add-func" @click="layerShow">加入购物车</div>
        <div class="shopping-cart-func purchase-func" @click="layerShow">立即购买</div>
    </div>    
</template>

<script>
export default {
    name: "ShoppingCartNav",
    methods: {
        layerShow() {
            this.$emit('show',true);
        }
    }
}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin'
    .shopping-cart-nav
        max_width() 
        z-index 5
        position fixed
        bottom 0
        height 1rem
        background-color $white
        .small-func
            align_vertical()
            float left
            color $darkGray
            width (40/3)%
            height 1rem
            font-size .24rem
            &-icon
                color #666
                display block
                font-size .44rem
                margin-bottom .1rem
                &.customer-service
                    color #4A90E2
                &.collection
                    color $gray
        .shopping-cart-func
            vertical(1rem)
            color $white
            float left
            width 30%
            text-align center
            font-size .3rem
            &.add-func
                background-color #FF9500
            &.purchase-func
                background-color $red
</style>



