<template>
    <div class="commodity-detail">
        <div class="commodity-detail-head">
            <div class="commodity-detail-head-wrap" @click="back"><span class="iconfont head-icon">&#xe669;</span></div>
            <router-link to="/shopping_cart" class="commodity-detail-head-wrap"><span class="iconfont head-icon">&#xe600;</span></router-link>
        </div>
        <swiper :options="swiperOption">
            <swiper-slide>
                <div class="swiper-commodity">
                    <img :class="['swiper-commodity-pic', {'width-alone' : widthSty}]" :src="commodityItem.image" :alt="commodityItem.goodsName">
                </div>
            </swiper-slide>
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
    </div>
</template>

<script>
export default {
    name: "DetailSwiper",
    data() {
        return {
            swiperOption: {
                pagination: '.swiper-pagination',
                paginationClickable: true,
                paginationType: 'fraction'
            }
        }
    },
    props: {
        commodityItem: {
            type: Object,
            required: true,
            default() {
                return {}
            }
        },
        type: {
            type: String,
            required: true
        }
    },
    computed: {
        widthSty() {
            return this.type == 'classify' ? true : false;
        }
    },
    methods: {
        back() {
            this.$router.go(-1);
        }
    }
}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin'
    .commodity-detail
        margin-bottom 0.15rem
        detail_top()
        & >>> .swiper-commodity
            width 100%
            height 0
            padding-bottom 67%
            overflow hidden
            background-color $white
            &-pic
                max-width 100%
                height 5.1rem
            .width-alone
                width 100%
                max-width 7.5rem
        & >>> .swiper-pagination
            vertical(.5rem)
            color $white
            width 1.1rem
            left 6.2rem
            border-radius .8rem
            font-size .3rem
            background-color rgba(0,0,0,.5)
</style>


