import Vue from 'vue'
import Router from 'vue-router'
import Home from '~home/Home'
import Classify from '~classify/Classify'
import MemberCenter from '~member_center/MemberCenter'
import ShoppingCart from '~shopping_cart/ShoppingCart'
import HomeCommodityDetail from '~commodity_detail/HomeCommodityDetail'
import ClassifyCommodityDetail from '~commodity_detail/ClassifyCommodityDetail'
import NotFound from '~not_found/NotFound'
import AddressManage from '~other/AddressManage'
import AddrModify from '~other/AddrModify'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  base: '/Project/shopping_mall/',
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/classify',
      name: 'Classify',
      component: Classify
    },
    {
      path: '/member_center',
      name: 'MemberCenter',
      component: MemberCenter
    },
    {
      path: '/shopping_cart',
      name: 'ShoppingCart',
      component: ShoppingCart
    },
    {
      path: '/home/:type/:id',
      name: 'HomeCommodityDetail',
      component: HomeCommodityDetail
    },
    {
      path: '/classify/:bigClassify/:smallClassify/:id',
      name: 'ClassifyCommodityDetail',
      component: ClassifyCommodityDetail
    },
    {
      path: '/address_manage',
      name: 'AddressManage',
      component: AddressManage
    },
    {
      path: '/addr_modify/:type',
      name: 'AddrModify',
      component: AddrModify
    },
    {
      path: '*',
      name: 'NotFound',
      component: NotFound
    }
  ]
})



