<template>
    <div class="member-center-head" :style="memberBg">
        <van-row :gutter="15">
            <van-col :span="5">
                <van-row class="head-portrait">
                    <span class="iconfont head-portrait-icon">&#xe711;</span>
                </van-row>
                <van-row>
                    <van-button class="grade-btn">大众</van-button>
                </van-row>
            </van-col>
            <van-col :span="12">
                <van-row class="fodbean">
                    我的福豆: <span class="fodbean-number">29</span>
                    <span class="iconfont fodbean-icon" >&#xe61c;</span>
                </van-row>
                <van-row>
                    <van-steps :active="activeIndex">
                        <van-step>1</van-step>
                        <van-step>2</van-step>
                        <van-step>3</van-step>
                        <van-step>4</van-step>
                        <van-step>5</van-step>
                    </van-steps>
                </van-row>
            </van-col>
            <van-col :span="7">
                <van-button class="sign-in">
                    <span class="sign-in-txt">连续签到</span>
                    <span class="sign-in-days"><span class="days">2</span>天</span>
                </van-button>
            </van-col>
        </van-row>
    </div>
</template>

<script>
export default {
    name: "MemberCenterHead",
    data() {
        return {
            activeIndex: 1,
            memberBg: {
                background: `url(' ${ require('~static/images/member-center-bg.png') } ') no-repeat #000`,
                backgroundSize: 'cover'
            }
        }
    }
}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin'
    .member-center-head
        height 2.2rem
        padding .2rem .2rem 0 .2rem
        box-sizing border-box
        & >>> .head-portrait
            align_vertical()
            width 1.2rem
            height 1.2rem
            border-radius 50%
            margin auto
            background-color $white
            &-icon
                color #CB9950
                font-size .8rem
        & >>> .grade-btn
            border_none()
            height .44rem
            line-height .52rem
            color $white
            width 1rem
            font-size .28rem
            padding 0
            margin-top .15rem
            border-radius .2rem
            background-color $darkRed
        & >>> .fodbean
            color $white
            font-size .28rem
            text-align left
            margin .2rem 0
            &-number
                color $white
                font-size .4rem
            &-icon
                color $yellow
                font-size .4rem
        & >>> .sign-in
            color $darkRed
            height .8rem
            line-height .34rem
            width 1.9rem
            font-size .26rem
            border-radius 1rem
            padding .1rem .2rem
            margin-top .5rem
            &-txt
                float left
                width 40%
                height 1rem
                margin-left 5%
            &-days
                float left
                width 55%
                margin-top .15rem
                .days
                    font-size .6rem
        & >>> .van-steps--horizontal
            padding 0
            text-align left
            background-color transparent
            .van-steps__items
                margin 0 
                &.van-steps__items--alone
                    padding-top 0
                    .van-step--finish
                        color $white
                    .van-step__circle-container
                        background-color transparent 
</style>



