<template>
    <ul class="home-link-list">
        <li v-for="(c,index) in category" :key="c.mallCategoryId">
            <router-link :to="setLink(index)" class="home-link" @click.native="setIndex(index)">
                <div class="home-link-wrap">
                    <img class="link-pic" :src="c.image" :alt="c.mallCategoryName">
                </div>
                <div class="link-txt" v-text="c.mallCategoryName"></div>
            </router-link>
        </li>
    </ul>
</template>

<script>
import { mapMutations } from 'vuex'
export default {
    name: 'HomeTab',
    props: {
        category: {
            type: Array,
            required: true,
            default() {
                return [] 
            }
        }
    },
    methods: {
        ...mapMutations(['setActiveIndex']),
        setLink(idx) {      //临时处理 ‘营养奶品’、‘食品饮料’、‘个人护理’ 3个未写的模块
            return idx < 2 ? '/classify' : '';
        },
        setIndex(idx) {
            this.setActiveIndex(idx);
        }
    }
}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin'
    .home-link-list 
        overflow hidden
        height 1.8rem
        background-color $white
        border_bold(before)
        & >>> .home-link
            float left
            width 20%
            &-wrap
                overflow hidden
                height 0
                padding-bottom 60%
                .link-pic
                    width 60%
            .link-txt
                ellipsis(1.3rem)
                color $darkGray
                font-size .26rem
                line-height .3rem
                margin .2rem auto 0
</style>



