<template>
    <div class="floor">
        <slot></slot>
        <h2 class="floor-tit" v-text="floorName"></h2>
        <ul class="floor-list">
            <li class="floor-list-item border-bottom" v-for="(f,index) in floors" :key="f.goodsId">
                <router-link :to="`/home/${floorFlag}/${f.goodsId}`" class="floor-link">
                    <div :class="['floor-wrap', alone(index)]">
                        <img class="floor-pic" :src="f.image" :alt="f.goodsName">
                    </div>
                </router-link>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    name: 'ShopFloor',
    props: {
        floors: {
            type: Array,
            required: true,
            default() {
                return []
            }
        },
        floorName: {
            type: String,
            required: true,
            default: ''
        },
        floorFlag: {
            type: String,
            required: true,
            default: ''
        }
    },
    methods: {
        alone(index) {
            if(index == 0) {
                return 'floor-wrap-alone border-right';
            }else if(index == 3) {
                return 'border-right';
            }
        }
    }
}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin'
    .floor
        position relative
        height 6.65rem
        overflow hidden
        &-slot
            color $darkRed 
            font-size .56rem 
            position absolute
            top 3%
            left 34%
        &-tit
            color $darkRed
            font-size .3rem
            font-weight bold
            vertical(.9rem)
        &-list
            height 6.65rem
            overflow hidden
            background-color $white
            &-item
                float left
                width 50%
                ^[0]-link
                    display block
                    ^[0]-wrap
                        overflow hidden
                        width 100%
                        height 0
                        padding-bottom 52.11%
                    ^[0]-wrap-alone
                        padding-bottom 104.2%
                        ^[0]-pic
                            width 100%
</style>




