<template>
    <div>
        <div class="detail">
            <div class="activity-price">
                <span class="price-wrap">
                    &#165;<span class="price" v-text="commodityItem.mallPrice"></span>-<span class="price" v-text="rushPrice"></span>
                </span>
                <span class="activity-price-txt">新品抢鲜价</span>
            </div>
            <div class="origin-price">
                <span>价格:</span>
                <span class="origin-price-wrap">
                    &#165;<span v-text="commodityItem.price"></span>-<span v-text="originPrice"></span>
                </span>
            </div>
            <div class="commodity-name" v-text="commodityItem.goodsName"></div>
            <div class="express">
                <div class="express-related express-related">快递：<span>0.0</span></div>
                <div class="express-related sales-volume">月销量 <span>34342件</span></div>
                <div class="express-related addr-info">福建漳州</div>
            </div>
        </div>
        <div class="detail">
            <van-cell-group>
                <van-cell>
                    <template slot="title">
                        <span class="discount">优惠</span>
                        <span class="coupon" :style="couponBg"></span>
                        <span>领取优惠券</span>
                    </template>
                    <div slot="right-icon" class="right-icon">
                        领券<span class="iconfont">&#xe667;</span>
                    </div>
                </van-cell>
                <van-cell>
                    <template slot="title">
                        <span class="discount discount-alone">促销</span>
                        <div class="promotion promotion-alone">
                            <div>
                                <span class="promotion-sign">促销</span><span>满39元省10元</span>
                            </div>
                            <div>
                                <span class="promotion-sign">积分</span><span>购买可得19积分</span>
                            </div>
                        </div>
                    </template>
                    <div slot="right-icon" class="right-icon">
                        <span class="iconfont">&#xe667;</span>
                    </div>
                </van-cell>
                <van-cell>
                    <template slot="title">
                        <span class="discount">服务</span>
                        <span>坏单包退 &#183; 正品保证 &#183; 不支持7天退换 &#183; 极速退款</span>
                    </template>
                    <div slot="right-icon" class="right-icon">
                        <span class="iconfont">&#xe667;</span>
                    </div>
                </van-cell>
            </van-cell-group>
        </div>
        <div class="detail">
            <van-cell-group>
                <van-cell>
                    <template slot="title">
                        <span class="discount">选择</span>
                        <span>请选择颜色分类</span>
                    </template>
                    <div slot="right-icon" class="right-icon">
                        <span class="iconfont">&#xe667;</span>
                    </div>
                </van-cell>
                <van-cell>
                    <template slot="title">
                        <span class="discount">参数</span>
                        <span>品牌</span>
                        <span>流行元素</span>
                    </template>
                    <div slot="right-icon" class="right-icon">
                        <span class="iconfont">&#xe667;</span>
                    </div>
                </van-cell>
            </van-cell-group>
        </div>
    </div>
</template>

<script>
export default {
    name: "DetailContent",
    data() {
        return {
            couponBg: {
                background: `url(' ${ require('~static/images/coupon.png') } ') no-repeat #ff0036`,
                backgroundSize: '100% 100%'
            }
        }
    },
    props: {
        commodityItem: {
            type: Object,
            required: true,
            default() {
                return {}
            }
        }
    },
    computed: {
        rushPrice() {
            let rush = this.commodityItem.mallPrice;
            return rush == undefined ? '' : (Number(rush)+10.2).toFixed(1);
        },
        originPrice() {
            let origin = this.commodityItem.price;
            return origin == undefined ? '' : (Number(origin)+10.2).toFixed(1);
        }
    }
}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin'
    .detail
        color #333
        padding 0 .2rem
        text-align left
        background-color $white
        margin-bottom .15rem
        .activity-price
            height .7rem
            color $red
            font-size .3rem
            overflow hidden
            .price-wrap
                float left
                line-height .7rem
                .price
                    font-size .5rem
            &-txt
                color $white
                float left
                margin-left .2rem
                font-size .2rem
                padding .08rem .06rem .05rem
                border-radius .2rem
                margin-top .2rem
                background-color $red
        .origin-price
            color $darkGray
            font-size .28rem
            &-wrap
                text-decoration line-through
        .commodity-name
            font-size .3rem
            margin-top .1rem
            line-height .4rem
        .express
            color $gray
            font-size .24rem
            padding .2rem 0
            overflow hidden
            &-related
                float left
                width (7.1/3)rem
                &.express-related
                    text-align left
                &.sales-volume
                    text-align center
                &.addr-info
                    text-align right
        & >>> .van-cell
            padding .15rem 0
            &:after
                border-style none
            .van-cell__title
                color #555
                font-size .24rem
                position relative
                .discount
                    color $gray
                    margin-right .1rem
                .coupon
                    display inline-block
                    width .7rem
                    height .2rem
                    border .06rem solid $red
                    margin-bottom -.04rem
                .discount-alone
                    position absolute
                    top 0
                    left 0
                .promotion
                    display inline-block
                    &.promotion-alone
                        margin-left .65rem
                    &-sign
                        color $red
                        margin-right .2rem
            .right-icon
                color $gray
                font-size .26rem
                >span 
                    font-size .26rem
</style>



