<template>
    <div class="detail-box">
        <detail-swiper :commodityItem="commodityItem" type="classify"></detail-swiper>
        <detail-content :commodityItem="commodityItem"></detail-content>
        <shopping-cart-nav @show="setLayerShow"></shopping-cart-nav>
        <mask-layer :commodityItem="commodityItem" :layerFlag="layerFlag" @close="setLayerShow"></mask-layer>
    </div>    
</template>

<script>
import { request } from '@/assets/javascript/request.js'
import DetailSwiper from './components/DetailSwiper'
import DetailContent from './components/DetailContent'
import ShoppingCartNav from '@/common/ShoppingCartNav'
import MaskLayer from './components/MaskLayer'
export default {
    name: "ClassifyCommodityDetail",
    data() {
        return {
            commodity: [],
            layerFlag: false
        }
    },
    components: {
        DetailSwiper,
        DetailContent,
        ShoppingCartNav,
        MaskLayer
    },
    computed: {
        bigClassifyType() {   //获取一级分类
            return this.$route.params.bigClassify;
        },
        smallClassifyType() {   //获取二级分类
            return this.$route.params.smallClassify;
        },
        goodsId() {     //获取商品id
            return this.$route.params.id;
        },
        commodityItem() {   //从二级分类中筛选，获取商品id对应的项
            let result = this.commodity.filter((item) => 
                item.goodsId == this.goodsId
            );
            return result[0];
        }
    },
    methods: {
        init() {
            this.$axios.get(request.url.classify).then(this.getData);
        },
        filterData(arr) {       //从一级分类中筛选
            let classify = arr.filter((item) => 
                item.categoryEnglishName == this.smallClassifyType
            );
            this.commodity = classify[0].commoditys;
        },
        getData(res) {
            if(res.status == 200 && res.data.data) {
                res = res.data.data.classify;
                switch(this.bigClassifyType) {
                    case 'fruit':   //水果
                        this.filterData(res[0].fruitCategory);
                        break;
                    case 'alcohol':  //酒类
                        this.filterData(res[1].alcoholCategory);
                        break;
                }
            }
        },
        setLayerShow(flag) {
            this.layerFlag = flag;
        }
    },
    created() {
        this.init();
    }
}
</script>

<style lang="stylus" scoped>
    .detail-box
        padding-bottom 1rem
</style>



