<template>
    <div class="home-swiper" v-if="swiperFlag">
        <swiper :options="swiperOption" ref="mySwiper" v-if="slidesLength">
            <swiper-slide v-for="slide in slides" :key="slide.goodsId">
                <div class="home-swiper-wrap">
                    <img class="swiper-pic" :src="slide.image" :alt="slide.goodsName">
                </div>
            </swiper-slide>
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
    </div>
</template>

<script>
export default {
    name: 'HomeSwiper',
    props: {
        slides: {
            type: Array,
            required: true,
            default() {
                return []
            }
        }
    },
    data() {
        return {
            swiperFlag: true,
            swiperOption: {
                loop: true,
                autoplay: 2500,
                autoplayDisableOnInteraction: false,
                pagination: '.swiper-pagination',
                paginationType: 'bullets',
                paginationClickable: true,
                onClick: () => {    //解决循环轮播状态下拖拽首尾，无法跳转到正确路由的bug
                    // swiper.realIndex ---> 当前轮播项swiper的活动索引
                    let swiper = this.$refs.mySwiper.swiper;
                    let url = `/home/slides/${ this.slides[swiper.realIndex].goodsId }`;
                    this.$router.push(url);
                }
            }
        }
    },
    computed: {
        slidesLength() {
            return this.slides.length;
        }
    },
    activated() {       //用于解决切换路由后，不会自动轮播的bug
        this.swiperFlag = true;
    },
    deactivated() {     //用于解决切换路由后，不会自动轮播的bug
        this.swiperFlag = false;
    }
}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin'
    .home-swiper
        width 100%
        height 0
        padding-bottom 44.45%
        background-color $gray
        & >>> .swiper-slide
            ^[0]-wrap
                display block
                .swiper-pic
                    width 7.5rem

</style>



