<template>
    <div class="shopping-cart">
        <van-nav-bar fixed :z-index="5">
            <template slot="left">
                <span class="cart-tit">购物车</span>
                <span class="cart-number">共<span v-text="goodsArr.length"></span>件宝贝</span>
            </template>
            <div slot="right" v-if="goodsArr.length" @click="setOperation" v-text="operation"></div>
        </van-nav-bar>
        <van-card class="commodity-card border-bottom"
            v-show="goodsArr.length"
            v-for="goods in goodsArr"
            :key="goods.goodsId"
            desc="原味包装"
            :thumb="goods.image"
            :title="goods.goodsName" 
            :price="goods.mallPrice" 
            :origin-price="goods.price"
        >
            <template slot="footer">
                <van-checkbox v-model="goods.goodsFlag" @change="getTotal"></van-checkbox>
                <van-stepper v-model="goods.number" integer class="commodity-step" @change="getTotal"></van-stepper>
            </template>
        </van-card>
        <shopping-cart-manage :goodsArr="goodsArr" :result="result" @all-checked="allChecked" @refresh-data="refreshData"></shopping-cart-manage>
        <recommend :hotGoods="hotGoods"></recommend>
        <footer-nav></footer-nav>
    </div>
</template>

<script>
import { request } from '@/assets/javascript/request.js'
import { mapState } from 'vuex'
import Recommend from './components/Recommend'
import ShoppingCartManage from './components/ShoppingCartManage'
import FooterNav from '@/common/FooterNav'
export default {
    name: "ShoppingCart",
    data() {
        return {
            hotGoods: [],
            checkedArr: [],
            operation: '管理',
            result: {
                total: 0,
                length: 0,
                flag: false,
                manageFlag: true
            }
        }
    },
    components: {
        Recommend,
        ShoppingCartManage,
        FooterNav
    },
    computed: {
        ...mapState(['goodsArr'])
    },
    methods: {
        init() {
            this.$axios(request.url.home).then(this.getHotGoods);
        },
        getHotGoods(res) {
            res = res.data.data;
            this.hotGoods = res.hotGoods;
        },
        getTotal() {    
            this.checkedArr = this.goodsArr.filter(item => item.goodsFlag == true);
            //结算数量合计
            this.result.length = this.checkedArr.length;
            //总价格合计
            let sum = 0;
            if(this.checkedArr.length){
                this.checkedArr.forEach((item) => {
                    let result = Number(Number(item.mallPrice * item.number).toFixed(2));
                    sum+=result;
                    this.result.total = sum;
                });
            }else {
                this.result.total = 0;
            }
            //判断商品是否全部选中
            if(this.checkedArr.length == this.goodsArr.length) {
                this.result.flag = true;
            }else {
                this.result.flag = false;
            }
        },
        allChecked(flag) {  //设置全选
            if(flag){
                this.goodsArr.forEach(item => {
                    item.goodsFlag = true;
                });
            }else {
                this.goodsArr.forEach(item => {
                    item.goodsFlag = false;
                });
            }
        },
        setOperation() {    //设置操作模式---管理/完成
            this.operation = this.operation == '管理' ? '完成' : '管理';
            this.result.manageFlag = !this.result.manageFlag;
        },
        refreshData() {     //删除商品后，恢复初始状态
            this.setOperation();
            this.result.manageFlag = true;
            this.result.total = 0;
            this.result.length = 0;
        }
    },
    mounted() {
        this.init();
    }
}
</script>

<style lang="stylus" scoped>
    @import '~stylus/mixin'
    .shopping-cart
        padding-top .8rem
        padding-bottom 1rem
        background-color $lightGray
        & >>> .van-nav-bar
            max_width()
            vertical(1rem)
            color $white
            right 0
            background-color $violet
            .van-nav-bar__left
                .cart-tit
                    font-size .38rem
                    font-weight 800
                    margin-right .1rem
                .cart-number
                    font-size .26rem
            .van-nav-bar__right
                font-size .3rem
                font-weight 800
        & >>> .commodity-card
            height 2.5rem
            padding .3rem .2rem 0 .68rem
            margin-top .1rem
            background-color $white
            position relative
            .van-card__thumb
                align_vertical()
                width 1.82rem
                height 1.82rem
                overflow hidden
                border .02rem solid $lightGray
                >img
                    width 100%
                    height inherit
            .van-card__content
                height 0
                padding-bottom 29%
                text-align left
                .van-card__title
                .van-card__desc
                    max-height .8rem
                    line-height .4rem
                    margin-bottom .1rem
                .van-card__title
                    color $violet
                    font-size .3rem
                    font-weight normal
                    margin-top .1rem
                .van-card__desc
                    font-size .26rem
                .van-card__bottom
                    position static
                    font-size .32rem
                    .van-card__origin-price
                        font-size .26rem
                        margin-left .2rem
            .van-card__footer
                full_screen(2,absolute,0,0,0,0,transparent)
                .van-checkbox
                    van_checkbox()
                    position absolute
                    top 50%
                    left .1rem
                    margin-top -.25rem
                .commodity-step
                    steper(.5rem,.5rem)
                    position absolute
                    right .2rem
                    bottom .4rem
</style>



